@import "../../global";

.pilih-kelas {
  display: flex;
  align-items: center;
  background-color: $color-grey;
  padding: 6vh 0;
  .card-wrapper {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-gap: 1rem;
  }
}

@media only screen and (max-width: 800px) {
  .pilih-kelas {
    .card-wrapper {
      grid-template-columns: auto auto;
    }
  }
}

@media only screen and (max-width: 500px) {
  .pilih-kelas {
    .card-wrapper {
      grid-template-columns: auto;
    }
  }
}
